import createApi from './createApi';

const api = createApi();

const banProbe = probeId => api.post('BanProbe', probeId);
const unbanProbe = probeId => api.post('UnbanProbe', probeId);
const uninstallProbe = probeId => api.post('UninstallProbe', probeId);
const installProbeByAnsible = (values) => api.post('InstallProbesByAnsible', values);
const queryProbesInstallation = (probeIds) => api.post('QueryProbesInstallation', probeIds);

export default {
    banProbe,
    unbanProbe,
    uninstallProbe,
    installProbeByAnsible,
    queryProbesInstallation,
}